<template>
  <div class="ao-person-sksz">
    <div class="title">{{ txt[0] }}</div>
    <div class="line"></div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item :label="item.label" v-for="(item, index) in list" :key="index">
        <el-input v-model="form[item.key]" :placeholder="item.pl"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="save">{{ txt[1] }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { updateInfo } from '@/api/user'


export default {
  name: "AOAssetReturn",
  data() {
    return {
      form: {
        name: "",
        bank_name: "",
        bank_number: "",
        alipay_account: "",
        weixin_nickname: "",
        weixin_account: ""
      },
      txt: this.$t('person.sksz_txt'),
      list: this.$t('person.sksz_list')
    };
  },
  computed: {
    ...mapState(['name','bank_name', 'bank_number', 'alipay_account', 'weixin_nickname', 'weixin_account'])
  },
  mounted() {
    this.form.name = this.name
    this.form.bank_name = this.bank_name
    this.form.bank_number = this.bank_number
    this.form.alipay_account = this.alipay_account
    this.form.weixin_nickname = this.weixin_nickname
    this.form.weixin_account = this.weixin_account
  },
  methods: {
    save() {
      updateInfo(this.form).then(res => {
        this.$store.dispatch('updateInfo',this.form)
      })
    }
  }
};
</script>

<style lang="scss">
.ao-person-sksz {
  .title {
    width: 100%;
    line-height: 60px;
    background: #1a243b;
    border-radius: 4px;
    padding-left: 20px;
    color: #c7cce6;
    margin-top: -34px;
  }
  .line{
      height: 20px;
    background: #273041;
    margin: 0 -34px;
    font-size:18px;
  }
  .el-form-item__label {
    color: #c7cce6;
  }
  .el-form-item {
    margin-bottom: 0;
  }
  .el-input__inner {
    width: 100%;
    border: 1px solid #6b80ae;
    border-radius: 3px;
    padding: 15px;
    color: #c7cce6;
    margin-bottom: 30px;
    background: none;
    font-size: 12px;
  }
  .el-button {
    flex: 1;
    width: 200px;
    line-height: 46px;
    background-color: #7a98f7;
    border-radius: 4px;
    color: #fff;
    border: none;
    padding: 0;
  }
}
</style>



